.book-detail-container {
    width: 100%; /* Full width */
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.error-message {
    color: red;
}

.book-detail-content {
    display: flex; /* Flexbox layout for book details and reviews */
    justify-content: space-between;
}

.book-info {
    flex: 1; /* Take up available space */
    margin-right: 20px;
}

.book-detail-table {
    width: 100%;
    margin-bottom: 20px; /* Spacing between the table and the Add Review link */
    border-collapse: collapse; /* Collapse borders for better styling */
}

.book-detail-table td {
    padding: 10px; /* Padding for table cells */
    border: 1px solid #ddd; /* Light border for table cells */
}

.add-review-link {
    display: inline-block; /* Display link as a block element */
    padding: 10px 15px;
    background-color: #007bff; /* Blue background */
    color: white; /* White text */
    text-decoration: none;
    border-radius: 5px;
}

.add-review-link:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.reviews-section {
    flex: 2; /* Take up more space than the book info */
}

.reviews-table {
    width: 100%;
    border-collapse: collapse; /* Collapse borders for better styling */
}

.reviews-table th,
.reviews-table td {
    padding: 10px; /* Padding for table cells */
    border: 1px solid #ddd; /* Light border for table cells */
    text-align: left; /* Align text to the left */
}

.review-date {
    font-style: italic; /* Italicize date for emphasis */
}
.back-button {
    padding: 10px 15px;
    background-color: #007bff; /* Blue background */
    color: white; /* White text */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px; /* Add margin to separate from content */
    font-size: 16px; /* Font size */
}

.back-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}
